#components-layout-demo-custom-trigger .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}

#components-layout-demo-custom-trigger .trigger:hover {
  color: #1890ff;
}

#components-layout-demo-custom-trigger .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}

.site-layout .site-layout-background {
  background: #fff;
}
.page-header{
  margin: 15px;
}
ul.header-left-section li{
  list-style: none;
  display: inline;
}

ul.header-left-section li:not(:first-child){
  margin-left: 10px;
}
.header-right-section{
  float: right;
  margin-right: 50px;
}
ul.header-right-section li{
  list-style: none;
  display: inline;
  padding-left: 20px;
}
